<template>
  <div id="app">
    <van-config-provider :theme-vars="themeVars">
    <router-view v-slot="{ Component }">
      <keep-alive>
        <component :is="Component"
                   v-if="$route.meta.keepAlive"
                   :key="$route.path" />
      </keep-alive>
      <component :is="Component"
                 v-if="!$route.meta.keepAlive" />
    </router-view>
    </van-config-provider>
  </div>

</template>

<script>
  import {reactive,onMounted, toRefs} from 'vue';
  import {useRouter} from 'vue-router';
  import moment from 'moment'
  moment.locale('zh-cn');
  // moment().format('LT');   // 14:44
  moment().format('LL');   // 2021年3月17日
  // moment().format('LTS');  // 14:44:16
  // moment().format('L');    // 2021/03/17
  // moment().format('l');    // 2021/3/17
  // moment().format('ll');   // 2021年3月17日
  // moment().format('LLL');  // 2021年3月17日下午2点44分
  // moment().format('lll');  // 2021年3月17日 14:44
  // moment().format('LLLL'); // 2021年3月17日星期三下午2点44分
  // moment().format('llll'); // 2021年3月17日星期三 14:44
export default {
  setup() {
    const router = useRouter()
    const state = reactive({
      transitionName: 'slide-left'
    })
    onMounted(()=>{
      window.onload=function () {
        document.addEventListener('touchstart',function (event) {
          if(event.touches.length>1){
            event.preventDefault();
          }
        });
        var lastTouchEnd=0;
        document.addEventListener('touchend',function (event) {
          var now=(new Date()).getTime();
          if(now-lastTouchEnd<=300){
            event.preventDefault();
          }
          lastTouchEnd=now;
        },false);
        document.addEventListener('gesturestart', function (event) {
          event.preventDefault();
        });
      }
      window.addEventListener('resize', function() {
        if (
            document.activeElement.tagName === 'INPUT' ||
            document.activeElement.tagName === 'TEXTAREA'
        ) {
          window.setTimeout(function() {
            if ('scrollIntoView' in document.activeElement) {
              document.activeElement.scrollIntoView(false)
            } else {
              document.activeElement.scrollIntoViewIfNeeded(false)
            }
          }, 0)
        }
      })
    })
    router.beforeEach((to, from) => {
      if (to.meta.index > from.meta.index) {
        state.transitionName = 'slide-left' // 向左滑动
      } else if (to.meta.index < from.meta.index) {
        // 由次级到主级
        state.transitionName = 'slide-right'
      } else {
        state.transitionName = ''   // 同级无过渡效果
      }
    })
    // themeVars 内的值会被转换成对应 CSS 变量
    // 比如 sliderBarHeight 会转换成 `--van-slider-bar-height`
    const themeVars = {
      navBarIconColor: '#000', // 导航栏返回按钮颜色
      navBarTextColor: '#3AAA8E', // 导航栏右侧文字颜色
      navBarArrowSize: '20px',  // 导航栏返回按钮大小
    };
    return {
      ...toRefs(state),
      themeVars,
    }
  }
}
</script>
