import {createApp} from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'vant/lib/index.css'; // 全局引入样式
import '@/common/style/reset.css'
import fastClick from 'fastclick'
fastClick.attach(document.body)
import 'lib-flexible/flexible'
import {
  ActionBar,
  Search,
  Picker,
  Uploader,
  Area,
  ActionBarIcon,
  ActionBarButton,
  Divider,
  NavBar,
  Popup,
  Overlay,
  Loading,
  Dialog,
  ContactCard,
  Form,
  AddressEdit,
  AddressList,
  Field,
  CellGroup,
  Cell,
  SwipeCell,
  Icon,
  Stepper,
  Card,
  Checkbox,
  CheckboxGroup,
  Button,
  Swipe,
  SwipeItem,
  PullRefresh,
  List,
  Tab,
  Tabs,
  SubmitBar,
  Toast,
  RadioGroup,
  Radio,
  Switch,
  DatetimePicker,
  Circle,
  Collapse,
  CollapseItem,
  Image as VanImage
} from 'vant'
import ECharts from 'vue-echarts'
import { use } from "echarts/core";
import { ConfigProvider } from 'vant';
// 手动引入 ECharts 各模块来减小打包体积
import {
  CanvasRenderer
} from 'echarts/renderers'
import {
  LineChart,
  BarChart
} from 'echarts/charts'
import {
  TitleComponent,
  GridComponent,
  TooltipComponent,
} from 'echarts/components'

use([
    LineChart,
  CanvasRenderer,
  BarChart,
  TitleComponent,
  GridComponent,
  TooltipComponent
]);

const app = createApp(App)

// 全局注册组件（也可以使用局部注册）
app.component('v-chart', ECharts)
app.use(router)
app.use(store)
app.use(ConfigProvider)
  .use(ActionBarButton)
  .use(Toast)
  .use(Uploader)
  .use(Area)
  .use(Picker)
  .use(Search)
    .use(NavBar)
  .use(ActionBarIcon)
  .use(ActionBar)
  .use(Divider)
  .use(Popup)
  .use(Overlay)
  .use(Loading)
  .use(Dialog)
  .use(ContactCard)
  .use(Form)
  .use(AddressEdit)
  .use(AddressList)
  .use(Field)
  .use(CellGroup)
  .use(Cell)
  .use(SwipeCell)
  .use(Icon)
  .use(Stepper)
  .use(Card)
  .use(Button)
  .use(Swipe)
  .use(SwipeItem)
  .use(PullRefresh)
  .use(List)
  .use(Tab)
  .use(Tabs)
  .use(SubmitBar)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(Radio)
  .use(RadioGroup)
  .use(Switch)
  .use(DatetimePicker)
  .use(Circle)
  .use(Collapse)
  .use(CollapseItem).use(VanImage)
app.mount('#app')
