import { createStore } from 'vuex'

/**
 * this.$store.commit('increment')
 * console.log(this.$store.state.count)
 * 使用方法
 */

const states = {
  count: 0,
  sportList: [], // 运动类型列表
  sportTypeObj: {}, // 运动键值对
}

const _mutations ={
  increment (state) {
    state.count++
  },
  sportList(state,sportList){
    states.sportList = sportList
  },
  sportTypeObj(state,sportTypeObj){
    states.sportTypeObj = sportTypeObj
  }
}

export default createStore({
  state () {
    return states
  },
  mutations: _mutations
})
